* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Comfortaa';
}

body {
  background-color: #161a2b;
}
.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #161a2b;
  text-align: center;
  margin: 0  auto 130px;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #edf5e1;
  font-size: 24px;
  
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #16ffbd;
  outline: none;
  width: 420px;
  background: transparent;
  color: #edf5e1;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background-color: #16ffbd;
  color: rgb(0, 0, 0);
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid #16ffbd;
}

.todo-button.edit {
  border-color: #2d283e;
  padding: 16px 22px;
  color: #161a2b;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: rgb(0, 0, 0);
  background-color: #16ffbd;

  padding: 16px;
  border-radius: 5px;
  width: 90%;
}




.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: #161a2b;
}

.delete-icon {
  margin-right: 5px;
 
}

